.facial-expression-container {
  width: 100%;
  height: 500px;
  margin: 20px 0;
  position: relative;
}

.expression-container {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 10px;
  border-radius: 5px;
}

.expression-label {
  margin-bottom: 5px;
}

.expression-value {
  margin: 0;
}
